.subscribtion-form {
  display: block;
  text-align: end;

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: flex-start;
  }

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
  }
}

.subscribtion-form__input {
  width: 100%;
  height: 42px;
  border-radius: 30px;
  color: var(--txt-cl);
  padding: 12px 12px;
  border: 0.854167px solid var(--subscribe-btn-bg-accent-cl);
  outline: transparent;

  &:invalid:not(:placeholder-shown) {
    border: 0.854167px solid var(--input-error-cl);
  }

  &:valid:not(:placeholder-shown) {
    border: 0.854167px solid var(--subscribe-btn-border-cl);
  }

  @media screen and (min-width: 768px) {
    width: 394px;
  }

  &::placeholder {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    font-weight: 400;
    font-size: 12px;
    line-height: calc(18 / 12);
    color: #505050;
    letter-spacing: 0.03em;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.subscribtion-form__input:invalid:not(:placeholder-shown)
  ~ .subscribtion-form__title {
  opacity: 1;
}

.subscribtion-form__title {
  display: none;
  width: 100%;
  margin-top: 4px;

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    opacity: 0;
  }
}

.subscribtion-form__text-error {
  text-align: left;
  font-size: 10px;
  line-height: calc(15 / 10);
  margin-left: 4px;
  margin-top: 4px;
  color: var(--input-error-cl);
}

.subscribtion-form__icon {
  text-align: center;
}

.subscribtion-form__btn {
  margin-top: 20px;

  @media screen and (min-width: 768px) {
    margin-top: 0px;
    margin-left: 18px;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 32px;
    margin-bottom: 0px;
  }
}

.subscribe_conteiner {
  // position: relative;
  // z-index: 100;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  margin-top: 0px;
  margin-bottom: 60px;

  @media screen and (min-width: 768px) {
    justify-content: end;
    margin-top: 20px;
    margin-bottom: 100px;
  }

  @media screen and (min-width: 1280px) {
    position: relative;
    bottom: 40px;
    flex-direction: row-reverse;
    flex-wrap: inherit;
    max-width: 100%;
    margin-bottom: 35px;
    padding-right: 18px;
    margin-top: 0;
    gap: 38px;
  }
}

.subscribe-pic {
  position: relative;
  bottom: 40px;
  text-align: center;
  margin-bottom: -23px;

  @media screen and (min-width: 768px) {
    bottom: 92px;
    margin-bottom: -72px;
    text-align: -webkit-right;
  }

  @media screen and (min-width: 1280px) {
    position: static;
    margin-bottom: 0px;
  }
}

.subscribtion__title {
  margin-bottom: 20px;

  font-size: 28px;
  line-height: 1.2;

  @media screen and (min-width: 768px) {
    font-size: 42px;
  }
}

.subscribtion__text {
  font-size: 14px;
  line-height: calc(16 / 14);

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.5;
  }

  margin-bottom: 40px;
  margin-top: 20px;

  @media screen and (min-width: 768px) {
    margin-bottom: 24px;
  }
}
